@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-style: normal;

  src: url("fonts/opensans.woff2") format("woff2"),
    url("fonts/opensans.woff2") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "Open Sans";
  font-style: normal;

  src: url("fonts/opensansbold.woff2") format("woff2"),
    url("fonts/opensansbold.woff2") format("woff");
}

@font-face {
  font-weight: 300;
  font-family: "Open Sans";
  font-style: normal;

  src: url("fonts/opensanslight.woff2") format("woff2"),
    url("fonts/opensanslight.woff2") format("woff");
}
